.profile-section{
    position: fixed;
        left: 0px;
        top: 0px;
        right: 552px;
        bottom: 0px;
    z-index: 1;
    width: 40%;
    height: 100%;
  
  
  
    
  .user-info *{
        transition: all 0.3s ease;
        opacity: 0;
        transform: translateX(-40px);

    &.bs{
        transform: translateX(0);
        opacity: 1;
    }
  }
}

.main-info{
    position: relative;
    overflow: visible;
    width: 60%;
    max-width: 1200px;
    margin-left: 40%;
    float: none;
    clear: none;
  
    &:after{
      position: absolute;
      content: '';
      top: 0; bottom: 0;
      left: 0; right: 0;
      transition: all 0.8s cubic-bezier(0.79, 0, 0.05, 0.83);;
      background-color: #fff;
    }
  
    &.active{
      &:after{
        position: absolute;
        content: '';
        top: 0; bottom: 0;
        left: 0; right: 0;
        transform: translateX(100%);
        background-color: #fff;
      }
    }
  }

  
  @media (max-width: 767px){
	.profile-section {
    position: static;
    width: 100%;
	}
	.main-info{
		width: 100%;
    margin-left: 0%;
	}

}
