//Post header

.header-post{
  width: 100%;
  height: 450px;
  position: relative;
  overflow: hidden;
  cursor: default;

  .overlay{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    width: 100%;
    height: 100%;

    &-filter1{
      background-image: url('dist/img/logo/filter.png');
    }

    &-filter2{
      background-image: url('dist/img/logo/filter2.png');
    }
  }


  .header-content{
    height: auto;

    &-30{
      background-size: 30%;
    }
    &-35{
      background-size: 35%;
    }
    &-40{
      background-size: 40%;
    }
    &-45{
      background-size: 45%;
    }
    &-50{
      background-size: 50%;
    }

    @media screen and (max-width: 992px){
      &-30{
        background-size: 37.5%;
      }
      &-35{
        background-size: 40%;
      }
      &-40{
        background-size: 42.5%;
      }

    }
  }

  .header-image{
    
  }

}
